<template>
  <div>
    <el-form label-width="80px">
      <el-form-item label="树洞内容">
        <span v-html="content"></span>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getApiBaseUrl } from '@/utils/api'

export default {
  name: 'Treehole',
  data () {
    return {
      content: ''
    }
  },
  mounted () {
    this.getTreehole()
  },
  methods: {
    getTreehole () {
      this.$http.get(getApiBaseUrl() + '/treehole_view.php', {params: {'id': this.$route.params['id']}}).then(response => {
        this.content = response.body.content
      }, response => {
      })
    }
  }
}
</script>

<style scoped>
</style>



