<template>
  <div>
    <el-form label-width="80px">
      <el-form-item label="树洞内容">
        <el-input type="textarea" v-model="content"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { getApiBaseUrl } from '@/utils/api'

export default {
  name: 'Index',
  data () {
    return {
      content: ''
    }
  },
  methods: {
    onSubmit () {
      this.$http.post(getApiBaseUrl() + '/treehole_submit.php', {'content': this.content}).then(response => {
        this.$alert('提交成功，您的吐槽已经被保存在 /#/view/' + response.body.id, '成功', {
          confirmButtonText: '确定'
        })
      }, response => {
        this.$alert('提交失败。', '失败', {
          confirmButtonText: '确定'
        })
      })
    }
  }
}
</script>

<style scoped>
</style>



